export default {
	data() {
		return {
			active: 1,
			info: [],
			infos: [],
			length: '',
			lengths: ''
		}
	},
	onLoad() {
		// 一级用户人员信息接口
		var _this = this
		// var url = _this.Global.url
		uni.request({
			url: 'https://exam.qhynice.top/index.php/Api/User/tree',
			method: 'POST',
			data: {
				uid: 44
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: res => {
				console.log(res);
				this.info = res.data.data;
				this.length = res.data.data.length;
			}
		});
	},
	methods: {
		// 二级用户人员信息接口
		onClick() {
			var that = this
			// var url = that.Global.url
			uni.request({
				url: 'https://exam.qhynice.top/index.php/Api/User/tree',
				method: 'POST',
				data: {
					uid: 44,
					level: 1
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
					this.infos = res.data.data;
					this.lengths = res.data.data.length;
				}
			});
		}
	}
};
